import * as React from 'react';
import { Controller, RenderEditableProps } from '@ali/4ever-cangjie';
import RegisterEvent from './components/RegisterEvent';
import { LaserPointerPluginConfig } from './types';

export default function createRenderEditable(config?: LaserPointerPluginConfig) {
  return (props: RenderEditableProps, controller: Controller, next) => {
    if (!config?.isLaserPointerEnabled || !config.isLaserPointerEnabled()) {
      return next();
    }

    return (
      <>
        {next()}
        <RegisterEvent
          controller={controller}
          getRole={config.getRole}
          getHighlightType={config.getHighlightType}
        />
      </>
    );
  };
}
